.app-logo {
  text-align: left;
  background-color: slategrey;
}

.logo {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 700;
  font-size: 300%;
  color: rgb(255, 255, 255);
}

.header-item {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 500;
}

